import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { tap, delay, take } from 'rxjs/operators';
import { environment } from '../../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class ControleEventosService {

  private readonly API_GET_LISTA_EVENTOS = `${environment.API}controleEventos/getListaEventos`;  
  private readonly API_GET_EVENTO = `${environment.API}controleEventos/getEvento`;  
  private readonly  API_GET_LISTA_INSCRITOS_EVENTOS = `${environment.API}controleEventos/getListaInscritosEvento`;    
  private readonly  API_CONFIRMAR_PARTICIPACAO_EVENTO = `${environment.API}controleEventos/confirmarParticipacaoEvento`;    
  private readonly  API_CANCELAR_PARTICIPACAO_EVENTO = `${environment.API}controleEventos/cancelarParticipacaoEvento`;    
  private readonly API_GERAR_CARTEIRA_PESSOA_FISICA = `${environment.API}pessoaFisica/carteira`;    
  private readonly API_SALVAR_EVENTO = `${environment.API}controleEventos/salvarEvento`;    
  private readonly API_EXCLUIR_EVENTO = `${environment.API}controleEventos/excluirEvento`;    
  private readonly API_LOAD_COMBOS = `${environment.API}tabelas/loadCombos`;    
  
  private httpOptions = {
    headers: new HttpHeaders({
      'X-Requested-With': 'XMLHttpRequest',
      'Cache-Control':	'no-cache',
      'Content-Type': 'application/json; charset=utf-8'
    })
  };

    
  constructor(private http: HttpClient) { }

  
  getListaEventos( search) {
    if (search !== ""){
      return this.http.post(this.API_GET_LISTA_EVENTOS,{campos:[{ campo: 'descricao', valor: search, operador: 'LIKE', condicao: ' OR ' }]}, this.httpOptions)
      .pipe(
         tap(console.log)
      );

    }else{
      return this.http.post(this.API_GET_LISTA_EVENTOS,{}, this.httpOptions)
      .pipe(
         tap(console.log)
      );

    }

      
  }

  getListaInscritosEvento( search) {
    if (search !== ""){
      return this.http.post(this.API_GET_LISTA_INSCRITOS_EVENTOS,{campos:[{ campo: 'id_evento', valor: search, operador: '=', condicao: ' OR ' }]}, this.httpOptions)
      .pipe(
         tap(console.log)
      );

    }else{
      return this.http.post(this.API_GET_LISTA_EVENTOS,{}, this.httpOptions)
      .pipe(
         tap(console.log)
      );

    }

      
  }
  
  getEvento( id_evento) {
        return this.http.post(this.API_GET_EVENTO,{campos:[{ campo: 'id', valor: id_evento, operador: '=', condicao: ' OR ' }]}, this.httpOptions)
      .pipe(
         tap(console.log)
      );

         
  }


  salvarEvento(form) {
    let formObj = form.getRawValue(); // {name: '', description: ''}

    let serializedForm = JSON.stringify(formObj);

    return this.http.post(this.API_SALVAR_EVENTO, serializedForm, this.httpOptions)
    .pipe(
       tap(console.log)
    );
  }

  excluirEvento(form) {
    let formObj = form.getRawValue(); // {name: '', description: ''}

    let serializedForm = JSON.stringify(formObj);

    return this.http.post(this.API_EXCLUIR_EVENTO, serializedForm, this.httpOptions)
    .pipe(
       tap(console.log)
    );
  }


  confirmarParticipacaoEvento(id_inscricao, id_evento) {

    return this.http.post(this.API_CONFIRMAR_PARTICIPACAO_EVENTO, {id : id_inscricao, eventoSelecionado: id_evento }, this.httpOptions)
    .pipe(
       tap(console.log)
    );
  }


  cancelarParticipacaoEvento(id_inscricao, id_evento) {

    return this.http.post(this.API_CANCELAR_PARTICIPACAO_EVENTO, {id : id_inscricao, eventoSelecionado: id_evento }, this.httpOptions)
    .pipe(
       tap(console.log)
    );
  }

    
}