import { RouterModule } from '@angular/router';
import { NgModule } from '@angular/core';
import { RegistroPfGuard } from '../guards/registroPf.guard';
import { ListaEventosComponent } from './listaEventos.component';
import { ListaInscritosEventoComponent } from './listaInscritosEvento.component';

const controleEventosRoutes = [
    {path: '', component: ListaEventosComponent}, 
    { path: ':id', component: ListaInscritosEventoComponent }
];
@NgModule({
    imports: [RouterModule.forChild(controleEventosRoutes)],
    exports: [RouterModule]
})
export class ControleEventosRoutingModule {}

